@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro:400,600,700);
.split,
.gutter.gutter-horizontal {
  float: left; }

.gutter.gutter-horizontal {
  cursor: ew-resize; }

.gutter {
  float: left;
  background-color: #eee;
  cursor: ew-resize; }

.CodeMirror {
  height: 100% !important; }

body {
  font-family: 'Source Code Pro', monospace;
  font-size: 14px;
  margin: 0;
  padding: 0;
  overflow: hidden; }

*,
*:before,
*:after {
  box-sizing: border-box; }

